import { useState } from "react";
import "./assets/css/style.css";
import { BUTTON } from "./components/button";
import CHOICE_ITEM from "./components/choice-item";
import { data } from "./data";

let history = new Array(data.length);

function App() {
  const [isCorrect, setIsCorrect] = useState<number>();
  const [selected, setSelected] = useState<number>();
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [mark, setMark] = useState(0);

  const onSelect = (id: number) => {
    setIsCorrect(data[currentQuestion].correct);
    setSelected(id);
    if (data[currentQuestion].correct === id) {
      setMark(mark + 1);
    }
    history[currentQuestion] = id;
    console.log(history);
  };
  const prevQuestion = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(currentQuestion - 1);
      setSelected(undefined);
      setIsCorrect(undefined);
    }
  };
  const nextQuestion = () => {
    if (currentQuestion < data.length - 1) {
      setCurrentQuestion(currentQuestion + 1);
      setSelected(undefined);
      setIsCorrect(undefined);
    }
  };

  return (
    <div className="App flex justify-center items-center min-h-screen">
      <div className="bg-white p-8 mx-4 w-full rounded-2xl shadow-lg">
        <div className="mb-8">
          <div className="flex justify-between">
            <span className="text-green-400 text-xl font-semibold mb-4">
              Câu {data.findIndex((el) => el.id === data[currentQuestion].id)}/
              {data.length}
            </span>
            <span className="text-green-400 text-xl font-semibold mb-4">
              Điểm {mark}/{data.length}
            </span>
          </div>
          <p className="font-semibold">
            Câu {currentQuestion + 1}: {data[currentQuestion].question}
          </p>
        </div>
        <div className="grid grid-rows-4 gap-4 mb-8">
          {/* <input type="checkbox" name="radio" id="" checked hidden /> */}
          {data[currentQuestion].answers.map((answer, index) => (
            <CHOICE_ITEM
              onSelect={onSelect}
              id={index}
              selected={selected}
              correct={isCorrect}
            >
              {answer}
            </CHOICE_ITEM>
          ))}
        </div>
        <div className="flex justify-start space-x-2">
          <BUTTON onClick={prevQuestion}>Câu trước</BUTTON>
          <BUTTON onClick={nextQuestion}>Câu sau</BUTTON>
        </div>
      </div>
    </div>
  );
}

export default App;
