import React from "react";

type PROPS = {
  children?: any;
  onClick: () => void;
};

export const BUTTON = (props: PROPS) => {
  return (
    <button
      onClick={props.onClick}
      className="py-3 px-6 rounded-full text-white bg-green-400 hover:bg-green-500 hover:shadow-lg duration-200"
    >
      {props.children}
    </button>
  );
};
