import cx from "classnames";
import { useEffect } from "react";

type PROPS = {
  id: number;
  onSelect: (id: number) => void;
  correct?: number;
  selected?: number;
  children?: any;
};
const CHOICE_ITEM = (props: PROPS) => {
  useEffect(() => {
    console.log(props.correct);
  }, [props.correct]);

  return (
    <div
      onClick={(e) => {
        props.onSelect(props.id);
        // setSelected(true);
      }}
      className={cx([
        "rounded-full py-3 px-6 shadow bg-white cursor-pointer border hover:shadow-lg transform  duration-200 flex items-center",
        {
          "bg-green-500 text-white":
            props.correct !== undefined &&
            props.correct === props.selected &&
            props.selected === props.id,
          "bg-red-500 text-white":
            props.correct !== undefined &&
            props.correct !== props.selected &&
            props.selected === props.id,
        },
      ])}
    >
      <p>{props.children}</p>
    </div>
  );
};

export default CHOICE_ITEM;
